.page
    background: #010C15
    height: 100vh
    width: 100vw
    padding: 54px
    display: flex
    justify-content: center
    align-items: center


.page__wrapper
    max-width: 1782px
    max-height: 941.32px
    width: 100%
    height: 100%
    border: 1px solid #1E2D3D
    border-radius: 8px
    background: #011627
    display: flex
    flex-direction: column
    justify-content: space-between
    overflow: hidden


@media screen and (max-width: 1440px) 
    .page
        padding: 30px

@media screen and (max-width: 540px) 
    .page
        padding: 15px
