.footer 
  display: flex
  justify-content: flex-start
  border-top: 1px solid #1e2d3d
  border-radius: 0 0 8px 8px
  background-color: #031627

.find 
  display: flex
  align-items: center
  justify-content: center
  width: 146px
  height: 52px
  font-style: normal
  font-weight: 450
  font-size: 16px
  line-height: 21px
  color: #607b96
  border-radius: 0 0 0 8px
  border-right: 1px solid #1e2d3d

.instagram 
  display: flex
  justify-content: center
  align-items: center
  width: 50px
  height: 52px
  color: #607b96
  border-left: 1px solid #1e2d3d
  border-right: 1px solid #1e2d3d

.facebook 
  display: flex
  justify-content: center
  align-items: center
  width: 50px
  height: 52px
  color: #607b96
  border-right: 1px solid #1e2d3d

.username 
  display: flex
  justify-content: center
  align-items: center
  gap: 10px
  width: min-content
  min-width: 52px
  height: 52px
  margin-left: auto
  border-left: 1px solid #1e2d3d
  border-radius: 0 0 8px 0
  font-style: normal
  font-weight: 450
  font-size: 16px
  line-height: 21px
  color: #607b96

