@import url('https://fonts.googleapis.com/css2?family=Fira+Code:wght@500&display=swap')

.card
    width: 100%
    max-width: 370.35px
    border-radius: 15px
    overflow: hidden

.card__top
    height: 145px
    position: relative

.card__img
    object-fit: cover
    width: 100%
    height: 100%

.card__bottom
    padding: 24px 31px 32px 31px
    background: #011221
    height: 100%

.card__bottom p 
    margin: 0
    margin-bottom: 23px
    font-size: 18px
    color: #607B96

.card__bottom .buttons
    display: flex
    justify-content: space-between

.h2
    font-size: 16px
    font-weight: 700
    line-height: 21px
    color: #5565E8
    margin-bottom: 14px
  
.span
    font-size: 16px
    font-weight: 450
    line-height: 21px
    color:#607B96
    white-space: nowrap

.iconsContainer 
    position: absolute
    top: 10px
    right: 10px
    display: flex
    gap:5px

.card 
    opacity: 0
    transition: opacity 1s ease

.card.show 
    opacity: 1
    margin: 0 auto

.buttons
    display: flex
    gap: 10px
    white-space: nowrap

@media (max-width: 720px) 
    .card__bottom 
        padding: 24px 15px 32px 15px
    
